import clsx from 'clsx';
import React, {
    useEffect,
    useRef,
    useCallback,
    useImperativeHandle,
    forwardRef,
    useState,
    useMemo
} from 'react';
import { MDCBanner } from '@material/banner';
import { BannerPropTypes, BannerDefaultProps } from './generated/propTypes';

const Banner = forwardRef((props, externalRef) => {
    const {
        airy,
        children,
        className,
        customTopAppBarHeight,
        dismissButtonLabel,
        drawer,
        elevated,
        fixed,
        open,
        media,
        onClosed,
        onClosing,
        onOpened,
        onOpening,
        onPrimaryClicked,
        onSecondaryClicked,
        primaryButtonDismissive,
        primaryButtonLabel,
        secondaryButtonDismissive,
        secondaryButtonLabel,
        style,
        surfaceVariant,
        topAppBar,
        topAppBarVariant,
        ...customProps
    } = props;

    const ref = useRef();
    const bannerRef = useRef();
    const [height, setHeight] = useState(0);
    const [isOpen, setIsOpen] = useState(open);

    useImperativeHandle(
        externalRef,
        () => ({
            open: isOpen,
            height,
            current: ref.current
        }),
        [height, isOpen]
    );

    useEffect(() => {
        bannerRef.current = new MDCBanner(ref.current);
        return () => {
            bannerRef.current.destroy();
        };
    }, [bannerRef]);

    const getReason = reason => {
        switch (reason) {
            case 0:
                return 'primaryClicked';
            case 1:
                return 'secondaryClicked';
            default:
                return 'programmatically';
        }
    };

    const handleOnClosing = useCallback(
        e => {
            onClosing && onClosing(getReason(e.detail.reason), e);
        },
        [onClosing]
    );

    useEffect(() => {
        bannerRef.current.listen('MDCBanner:closing', handleOnClosing);
        return () => {
            bannerRef.current.unlisten('MDCBanner:closing', handleOnClosing);
        };
    }, [bannerRef, handleOnClosing]);

    const handleOnClosed = useCallback(
        e => {
            onClosed && onClosed(getReason(e.detail.reason), e);
            setIsOpen(false);
        },
        [onClosed]
    );

    useEffect(() => {
        bannerRef.current.listen('MDCBanner:closed', handleOnClosed);
        return () => {
            bannerRef.current.unlisten('MDCBanner:closed', handleOnClosed);
        };
    }, [bannerRef, handleOnClosed]);

    useEffect(() => {
        bannerRef.current.listen('MDCBanner:opening', onOpening);
        return () => {
            bannerRef.current.unlisten('MDCBanner:opening', onOpening);
        };
    }, [bannerRef, onOpening]);

    const handleOnOpened = useCallback(() => {
        onOpened?.();
        setHeight(bannerRef.current.foundation.adapter.getContentHeight());
        setIsOpen(open === false || true);
    }, [onOpened, open]);

    useEffect(() => {
        bannerRef.current.listen('MDCBanner:opened', handleOnOpened);
        return () => {
            bannerRef.current.unlisten('MDCBanner:opened', handleOnOpened);
        };
    }, [handleOnOpened]);

    useEffect(() => {
        if (open) {
            !bannerRef.current.isOpen && bannerRef.current.open();
        } else {
            bannerRef.current.isOpen && bannerRef.current.close();
        }
    }, [open]);

    const handleWindowSizeChange = useCallback(() => {
        bannerRef.current && bannerRef.current.layout();
    }, []);

    useEffect(() => {
        /* eslint-disable no-undef */
        window.addEventListener('resize', handleWindowSizeChange);

        return () => {
            window.removeEventListener('resize', handleWindowSizeChange);
        };
        /* eslint-enable no-undef */
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const classNames = clsx(
        'lmnt',
        'lmnt-banner',
        'mdc-banner',
        'mdc-banner--mobile-stacked',
        airy && 'lmnt-banner--airy',
        fixed && 'lmnt-banner--fixed',
        topAppBar && 'lmnt-banner--top-app-bar',
        topAppBar &&
            topAppBarVariant === 'fixed' &&
            'lmnt-banner--top-app-bar-fixed',
        topAppBar &&
            topAppBarVariant === 'prominent' &&
            'lmnt-banner--top-app-bar--prominent',
        topAppBar &&
            topAppBarVariant === 'dense' &&
            'lmnt-banner--top-app-bar--dense',
        topAppBar &&
            topAppBarVariant === 'dense-prominent' &&
            'lmnt-banner--top-app-bar--dense-prominent',
        topAppBar &&
            topAppBarVariant === 'prominent-fixed' &&
            'lmnt-banner--top-app-bar--prominent-fixed',
        topAppBar &&
            topAppBarVariant === 'dense-fixed' &&
            'lmnt-banner--top-app-bar--dense-fixed',
        topAppBar &&
            topAppBarVariant === 'dense-prominent-fixed' &&
            'lmnt-banner--top-app-bar--dense-prominent-fixed',
        drawer && 'lmnt-banner--drawer',
        elevated && 'lmnt-banner--elevated',
        surfaceVariant && 'lmnt-banner--surface-variant',
        bannerRef.current && bannerRef.current.isOpen && 'mdc-banner--open',
        className
    );

    const bannerStyles = useMemo(
        () =>
            customTopAppBarHeight
                ? {
                      ...style,
                      top: customTopAppBarHeight,
                      marginTop: 0,
                      position: 'fixed'
                  }
                : style,
        [style, customTopAppBarHeight]
    );

    const innerStyles = useMemo(
        () => (customTopAppBarHeight ? { top: customTopAppBarHeight } : {}),
        [customTopAppBarHeight]
    );
    return (
        <div
            ref={ref}
            aria-hidden={!open}
            className={classNames}
            {...customProps}
            style={bannerStyles}
            role='banner'
        >
            <div
                className={clsx(
                    'lmnt-banner__inner',
                    fixed && !airy && 'mdc-banner__fixed'
                )}
                style={innerStyles}
            >
                <div
                    className='mdc-banner__content lmnt-banner__content'
                    role='status'
                    aria-live='assertive'
                >
                    <div className='mdc-banner__graphic-text-wrapper'>
                        {media && (
                            <div
                                className='lmnt-banner__media mdc-banner__graphic'
                                role='img'
                            >
                                {media}
                            </div>
                        )}
                        <div className='mdc-banner__text mdc-typography'>
                            {children || (
                                <p>Banner text should be provided locally.</p>
                            )}
                        </div>
                    </div>
                    <div className='mdc-banner__actions lmnt-banner__actions'>
                        {secondaryButtonLabel && (
                            <button
                                onClick={onSecondaryClicked}
                                className={clsx(
                                    'mdc-button',
                                    secondaryButtonDismissive &&
                                        'mdc-banner__secondary-action'
                                )}
                                type='button'
                            >
                                {secondaryButtonLabel}
                            </button>
                        )}
                        <button
                            tabIndex='0'
                            onClick={onPrimaryClicked}
                            className={clsx(
                                'mdc-button',
                                (primaryButtonDismissive ||
                                    (!primaryButtonDismissive &&
                                        !secondaryButtonDismissive)) &&
                                    'mdc-banner__primary-action'
                            )}
                            type='button'
                        >
                            {primaryButtonLabel}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
});

Banner.displayName = 'Banner';
Banner.propTypes = BannerPropTypes;
Banner.defaultProps = BannerDefaultProps;

export default Banner;
