import clsx from 'clsx';
import React, { forwardRef, useMemo } from 'react';
import {
    BannerMarginFixPropTypes,
    BannerMarginFixDefaultProps
} from './generated/propTypes';

const BannerMarginFix = forwardRef((props, externalRef) => {
    const {
        bannerId,
        bannerOpen,
        children,
        className,
        extraMargin,
        marginTop,
        style,
        ...customProps
    } = props;

    const styles = useMemo(
        () =>
            bannerOpen
                ? { ...style, marginTop: marginTop + extraMargin }
                : style,
        [bannerOpen, marginTop, extraMargin, style]
    );

    return (
        <div
            ref={externalRef}
            {...customProps}
            style={styles}
            className={clsx('lmnt-banner--margin-fix', className)}
        >
            {children}
        </div>
    );
});

BannerMarginFix.displayName = 'BannerMarginFix';
BannerMarginFix.propTypes = BannerMarginFixPropTypes;
BannerMarginFix.defaultProps = BannerMarginFixDefaultProps;

export default BannerMarginFix;
